@media only screen and (max-width: 900px) {
  .rotate {
    animation: rotation 8s infinite;
  }

  @keyframes rotation {
    0% {
      transform: rotatey(0deg);
    }
    100% {
      transform: rotatey(360deg);
    }
  }

  .currency-wrapper {
    width: 100%;
    height: 100%;
    position: relative;
    padding: 100px 0px;
    .currency-wrapper-inner {
      display: grid;
      margin: 0 auto;
      padding: 20px 0;
      position: relative;
      width: 980px;
      grid-template-columns: repeat(1, 1fr);
      gap: 30px;
      .left-container {
        display: flex;
        align-items: center;
        justify-content: center;
      }
      .right-container {
        form {
          padding: 10px 20px;
          box-shadow: 2px 2px 10px 0px rgb(172, 166, 166);
          border-radius: 10px;
          width: 70%;
          margin: auto;
          h2 {
            margin-bottom: 10px;
          }
          .right-container-dropdown {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin: 30px 0px;
            .select-container {
              display: flex;
              width: 100%;
            }
            .swap-icon {
              font-size: 60px;
              cursor: pointer;
            }
          }
          .msg-print {
            margin: 20px 0px;
            text-align: center;
          }
          .btn {
            width: 100%;
            height: 3rem;
            background-color: #cbb248;
            color: #fff;
            font-size: 1.15rem;
            cursor: pointer;
          }
        }
      }
    }
  }
}

